/* -------------------------------------------
    Shop Page
        - Shop Banner

        - Toolbox
            (support toolbox, toolbox-left, toolbox-right)
            (support label, select-box)
        - Toolbox Item
            (support full)
        - Layout Button
        - Select Menu
            (support as sidebar's widget < 992px)
            (support toggle)
        - Select Items
        - Toolbox Pagination
        - Toolbox Horizontal
        - Toolbox Navigation
            (support as sidebar-fixed)
            (please place sidebar before toolbox: to know sidebar's close status)
---------------------------------------------- */

// Shop Banner
.shop-banner-default,
.shop-boxed-banner {
  .banner-subtitle {
    margin-bottom: .6rem;
    font-size: 2em;
  }

  .banner-title {
    margin-bottom: 2.4rem;
    font-size: 4em;
    letter-spacing: -.035em;
  }

  .btn {
    padding: 1.2em 2.8em;
    border-color: #919EB2;

    i {
      vertical-align: middle;
      margin-left: .7rem;
      font-size: 2.2rem;
    }

    &:hover,
    &:active,
    &:focus {
      border-color: $white-color;
    }
  }
}

.shop-banner-default {
  padding: 7.5rem 5.7% 8.3rem;
}

.shop-boxed-banner {
  padding: 7.4rem 7% 8.4rem;
}


// Toolbox, Filter Clean Widget, Filter Price Widget

.toolbox,
.toolbox-left,
.toolbox-right,
.toolbox-item,
.toolbox-item-full {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.toolbox {
  justify-content: space-between;
}

.toolbox {
  font-size: 1.3rem;
  line-height: 1.5;
  padding: 3.5rem .2rem 1rem;
  transition: margin .4s;
  background-color: #fff;

  &.sticky-toolbox {
    padding-top: 3rem;
  }

  &,
  &-left,
  &-right {
    >*:not(:last-child) {
      margin-right: 1.8rem;
    }
  }

  label {
    margin-right: .8rem;
    font-weight: 600;
    text-transform: uppercase;
  }

  select {
    cursor: pointer;
    padding-top: .85rem;
    padding-bottom: .85rem;
    border-color: #ccc;
  }

  .select-box .form-control {
    color: #222;
  }

  .select-box::before {
    font-size: 1rem;
    right: 1rem;
  }

  // ToolBox Sort
  .toolbox-sort {
    .form-control {
      max-width: 15rem;
      padding-left: 1.4rem;
    }

    &::before {
      right: 1.4rem;
    }
  }

  // .left-sidebar-toggle {
  //     padding: .8em 1.04em;
  //     i {
  //         margin-bottom: .1rem;
  //         &::before {
  //             font-weight: 600;
  //         }
  //     }
  // }
}

.toolbox-item {
  flex-wrap: nowrap;
  margin-bottom: 1rem;

  label {
    color: $dark-color;
  }
}

.toolbox-item-full {
  flex-basis: 100%;
}

// Layout Button
.btn-layout {
  color: #dadada;
  font-size: 22px;
  padding: 0 2px;

  &:last-child {
    padding-right: 0;
  }

  &:hover,
  &.active {
    color: #333;
  }

  &+& {
    margin-left: 2px;
  }
}

// Select Menu
.select-menu {
  color: #222;

  &::before {
    right: 1.25em;
    font-size: 1.2rem;
    margin-top: 1px;
  }

  ul,
  ul::before {
    content: '';
    position: absolute;
    border: 1px solid $border-color-light;
    width: 11px;
  }

  ul {
    top: 100%;
    z-index: 100;
    width: 18rem;
    margin-top: 1rem;
    padding: 1rem 1.4rem 1rem;
    background: #fff;
    opacity: 0;
    visibility: hidden;

    &::before {
      top: -6px;
      left: 22px;
      height: 11px;
      border-width: 1px 0 0 1px;
      background: #fff;
      transform: rotate(45deg);
    }
  }

  &.opened>ul {
    opacity: 1;
    visibility: visible;
  }
}

.select-menu select,
.select-menu-toggle {
  position: relative;
  border: 1px solid #ccc;
  padding: .8rem 4rem .8rem 1.4rem;
  font-size: 1.2rem;
  line-height: 1.5;
  letter-spacing: -.03em;
}

.select-menu-toggle {
  border-radius: 2px;
}

.select-menu {
  select {
    max-width: 17.2rem;
    padding: .85rem 2.6rem .85rem .9rem;
  }
}

// Select Items
.select-item {
  padding: 6px .7rem 4px 1.2rem;
  color: #222;
  background: #eee;
  letter-spacing: -.01em;

  i {
    padding: .5rem .6rem .7rem;
    margin-left: 5px;
    font-size: 9px;
    vertical-align: middle;
  }
}

.select-items {
  // display: none;
  display: block;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: .4s;

  .select-item {
    transition: opacity .4s;
  }

  .toolbox+& {
    font-size: 1.2rem;
    margin: 0;
  }

  >* {
    display: inline-block;
    margin: 0 .8rem .5rem 0;
  }

  .filter-clean {
    margin-left: 1.2rem;
  }
}

.sticky-content-wrapper+.select-items {
  font-size: 1.2rem;
  margin: 2px 0 1.8rem;
}

// Toolbox Pagination
.toolbox-pagination {
  position: relative;
  margin-bottom: 1rem;
  padding: 2.5rem .2rem;
  border-top: 1px solid #e1e1e1;
}

// Toolbox Horizontal
.toolbox-horizontal {
  .show-info {
    color: #999;
    font-size: inherit;
  }

  &.fixed {

    .sidebar-overlay,
    .sidebar-content {
      min-height: 100vh;
    }
  }

  @include mq(lg) {
    .toolbox-left {
      padding-right: 1px;
      margin-right: -1px;
    }
  }
}

//Left Sidebar Toggle
.toolbox .toolbox-left .left-sidebar-toggle,
.sidebar-content .filter-actions .sidebar-toggle-btn,
.toolbox .top-sidebar-toggle,
.right-sidebar-toggle.btn-sm {
  padding: .7em .92em;
  margin-right: 2rem;
  font-weight: 700;
  line-height: 1.2;

  .d-icon-filter-2 {
    font-size: 1.4rem;

    &::before {
      font-weight: 600;
    }
  }
}

.toolbox .toolbox-left .left-sidebar-toggle {
  font-weight: 600;
}

.toolbox-item.right-sidebar-toggle {
  margin-right: 0;
}

// Navigation Style
.toolbox-wrap {
  display: flex;
  flex-direction: column-reverse;
  line-height: 1.3;

  // Toolbox
  .toolbox>* {
    line-height: 37px;
  }

  .toolbox-layout {
    display: flex;
  }

  // select {
  //     max-width: 16rem;
  //     padding-left: 3px;
  //     padding-right: 2.4em;
  //     text-transform: uppercase;
  //     font-weight: 600;
  // }

  option {
    text-transform: none;
  }

  .toolbox-layout {
    display: flex;
  }

  //Sidebar Content
  .sidebar-content {
    .widget-title {
      padding-bottom: 1rem;
    }
  }

  // Sidebar Content
  // .widget-title {
  //     padding-bottom: 1.3rem;
  //     border-top: 0;
  // }
  // .sidebar-fixed .sidebar-content {
  //     padding-bottom: 0;
  // }
}

.sticky-toolbox.fixed {
  position: fixed;
  background-color: #fff;
  padding: 1rem 2rem 0;
}

// Filter Action, Price Slider, Filter Clean
.widget .widget-body .filter-actions {
  display: block;
  padding: 3.2rem 0 1.5rem .2rem;
}

.filter-price-text {
  font-size: 1.3rem;
  color: $dark-color;

}

.filter-items a::before {
  content: '';
  position: absolute;
  border: 1px solid $grey-color;
  width: 18px;
  height: 18px;
  border-radius: 2px;
  top: 50%;
  transform: translateY(-50%);
  left: -2.7rem;
  font-size: .7em;
  font-weight: 900;
  font-family: 'Font Awesome 5 Free';
  text-align: center;
  text-indent: 1px; // issue
  line-height: 1.7em;

  @include only-for-retina(1.5) {
    text-indent: 0;
  }
}

// .sticky-sidebar-wrapper .widget:first-child {
// 	border-top: 3px solid $border-color-light;
// }

.toolbox .btn-link {
  padding: 0;
}

.filter-clean {
  font-size: 1.4rem;
  margin-left: auto;
  color: $dark-color;
  transition: .3s;

  &:hover {
    color: $primary-color;
  }
}

.toolbox-wrap .card-body .filter-clean {
  display: inline;
  opacity: 0;
}

.toolbox-wrap .expanded .filter-clean {
  opacity: 1;
}

.card.navigation-card {
  &.expanded {
    overflow: visible;
  }

  &:not(.collapsed)+.toolbox .toolbox-right {
    opacity: 0;
    transform: opacity .3s;
  }
}

// Shop Sidebar
.shop-sidebar {
  position: relative;

  // .sidebar-content {
  // 	padding: 3rem;
  // }

  // .filter-actions {
  // 	padding-top: 0;
  // }
  // issue : need?
  .btn-outline:not(:hover) {
    color: $primary-color;
    background: #fff;
  }

  .sidebar-toggle-btn,
  .left-sidebar-toggle {
    padding: .8em 1.04em;
  }

  //Filter action
  .filter-actions {
    display: flex;
    align-items: center;
    padding-right: .6rem;
    margin-bottom: 2rem;
  }
}

.btn-icon-right i {
  margin-left: .8rem;
  font-size: 1.8rem;
}

