.banner {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  font-size: 1rem
}

.banner figure img {
  display: block;
  width: 100%;
  height: auto
}

.banner.inner-banner:before {
  content: "";
  position: absolute;
  left: 1rem;
  right: 1rem;
  top: 1rem;
  bottom: 1rem;
  border: 2px solid hsla(0, 0%, 88.2%, .3);
  z-index: 1
}

.banner.inner-banner .banner-content {
  z-index: 2
}

.banner .banner-content {
  position: relative
}

.banner-radius {
  border-radius: 1rem;
  overflow: hidden
}

.banner-subtitle {
  letter-spacing: -.025em;
  line-height: 1
}

.banner-price-info,
.banner-title {
  line-height: 1
}

.banner-fixed>.banner-content,
.banner-fixed>.container,
.banner-fixed>.container-fluid {
  position: absolute;
  z-index: 1
