.pager-link {
  position: relative;
  color: #999;
  -moz-box-orient: vertical;
  -moz-box-direction: normal;
  flex-direction: column;
  -moz-box-align: start;
  align-items: flex-start;
  -moz-box-flex: 0;
  flex: 0 0 50%;
  max-width: 50%;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  font-size: 1.3rem;
  font-weight: 400;
